import React from "react";
import './home.css';


//### HOME ###
const Home = () => {


    return (

        <main className="Content" style={{
            height: 'calc(100vh - 36pt)',
            maxHeight: 'calc(100vh - 6vmin)',
            minWidth: '100%',
            overflowY: 'scroll'
        }}>
            <div className="testarea">
                <div id="testSplit">
                    <div className="split a">
                        {/*<button id="doSomething" onClick={() => doChange()}>Change</button>*/}

                    </div>
                    <div className="split b">
                        <div className="colorF" id="dark"></div>
                        <div className="colorF" id="main"></div>
                        <div className="colorF" id="light"></div>
                    </div>
                </div>
            </div>
        </main>

    );
};

